.Tooltip {
  position: relative;
  cursor: pointer;
}

.Tooltip-message {
  background-color: hsla(0, 0%, 10%, 0.9);
  border-radius: 3px;
  color: #fff;
  left: 4vw;
  margin-bottom: 5px;
  margin-left: -8vw;
  min-width: 160px;
  padding: 7px 10px;
  position: absolute;
  text-align: center;
  top: calc(-1.2em - 14px);
  white-space: nowrap;
}

.Tooltip-message:after {
  border-left: 5px solid transparent;
  border-right: 5px solid transparent;
  border-top: 5px solid hsla(0, 0%, 20%, 0.9);
  content: " ";
  font-size: 0;
  left: 6vw;
  line-height: 0;
  margin-left: -5px;
  position: absolute;
  top: calc(18px + 14px);
  width: 0;
}

.Tooltip-message {
  opacity: 0;
  pointer-events: none;
  visibility: hidden;
}

.Tooltip:hover > .Tooltip-message {
  opacity: 1;
  visibility: visible;
}

.bubbles-container {
  position: absolute;
  top: 0;
  left: 50%;
  width: 100%;
  max-width: 15rem;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  opacity: 0.75;
  overflow: visible;
  z-index: -1;
}

.bubbles {
  width: 100%;
  height: auto;
}

circle {
  stroke: rgb(194, 194, 255);
  fill: none;
}

.bubbles > g > g:nth-of-type(3n) circle {
  stroke: rgb(255, 255, 160);
}

.bubbles > g > g:nth-of-type(4n) circle {
  stroke: rgb(255, 179, 179);
}

.bubbles-small {
  overflow: visible;
}

.bubbles-small > g {
  -webkit-transform: translateY(2048px);
          transform: translateY(2048px);
  will-change: transform, opacity;
}

.bubbles-small g:nth-of-type(1) circle {
  -webkit-animation: wobble 2.3s infinite ease-in-out;
          animation: wobble 2.3s infinite ease-in-out;
}

@-webkit-keyframes wobble {
  33% {
    -webkit-transform: translateX(-50px);
            transform: translateX(-50px);
  }

  66% {
    -webkit-transform: translateX(50px);
            transform: translateX(50px);
  }
}

@keyframes wobble {
  33% {
    -webkit-transform: translateX(-50px);
            transform: translateX(-50px);
  }

  66% {
    -webkit-transform: translateX(50px);
            transform: translateX(50px);
  }
}

@-webkit-keyframes up {
  0% {
    opacity: 0;
  }

  10%,
  90% {
    opacity: 1;
  }

  100% {
    opacity: 0;
    -webkit-transform: translateY(-124px);
            transform: translateY(-124px);
  }
}

@keyframes up {
  0% {
    opacity: 0;
  }

  10%,
  90% {
    opacity: 1;
  }

  100% {
    opacity: 0;
    -webkit-transform: translateY(-124px);
            transform: translateY(-124px);
  }
}

